import React from 'react'
import Grid from 'react-bootstrap/lib/Grid'
import Panel from 'react-bootstrap/lib/Panel'
import Row from 'react-bootstrap/lib/Row'
//import Pi from '@pinetwork-js/sdk'
//import jQuery from 'jquery-min'
import {injectIntl} from 'react-intl'

import {setTitle} from '../lib/utils'
import Script from 'react-load-script'



class ShowAccount extends React.Component {


  constructor(props) {
    super(props)
    this.state = {
        scriptStatus: 'no',
    }
}

handleScriptCreate() {
  this.setState({scriptLoaded: false})
}
 
handleScriptError() {
  this.setState({scriptError: true})
}
 
handleScriptLoad() {
  this.setState({scriptLoaded: true, scriptStatus: 'yes'})
}



render() {
 
    const {formatMessage} = this.props.intl
    setTitle('ShowAccount')
    
    


    return (
      <Grid>
        <Row>
          <Panel header={formatMessage({id: 'MyAccount'})}>
          
        
        <Script url="https://sdk.minepi.com/pi-sdk.js"
        onCreate={this.handleScriptCreate.bind(this)}
        onError={this.handleScriptError.bind(this)}
        onLoad={this.handleScriptLoad.bind(this)}> 
       
        
        
        
        </Script>
        <div>动态脚本引入状态：{this.state.scriptStatus}</div>
          </Panel>
        </Row>
      </Grid>
    )
  }
 
}

export default injectIntl(ShowAccount)



